.flame {
  z-index: 10;
  transform-origin: 50% 90%;
  background-color: orange;
  border-radius: 10px / 25px 25px 10px 10px;
  width: 15px;
  height: 35px;
  animation: 1s ease-in-out infinite alternate flicker;
  position: absolute;
  top: -15px;
  left: 10px;
  box-shadow: 0 0 10px #ffa50080, 0 0 20px #ffa50080, 0 0 60px #ffa50080, 0 0 80px #ffa50080;
}

@keyframes flicker {
  0% {
    transform: skewX(5deg);
    box-shadow: 0 0 10px #ffa50033, 0 0 20px #ffa50033, 0 0 60px #ffa50033, 0 0 80px #ffa50033;
  }

  25% {
    transform: skewX(-5deg);
    box-shadow: 0 0 10px #ffa50080, 0 0 20px #ffa50080, 0 0 60px #ffa50080, 0 0 80px #ffa50080;
  }

  50% {
    transform: skewX(10deg);
    box-shadow: 0 0 10px #ffa5004d, 0 0 20px #ffa5004d, 0 0 60px #ffa5004d, 0 0 80px #ffa5004d;
  }

  75% {
    transform: skewX(-10deg);
    box-shadow: 0 0 10px #ffa50066, 0 0 20px #ffa50066, 0 0 60px #ffa50066, 0 0 80px #ffa50066;
  }

  100% {
    transform: skewX(5deg);
    box-shadow: 0 0 10px #ffa50080, 0 0 20px #ffa50080, 0 0 60px #ffa50080, 0 0 80px #ffa50080;
  }
}

